@import url("https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap");
:root {
  --bb-nav: #1E1E1E;
  --bb-pink: #FF5C90;
  --bb-pink-light: #EA45A0;
  --bb-green: #32806F;
  --bb-green-light: #00AD45;
  --bb-fluo: #64FF4F;
  --bb-blue: #3B86E9;
  --bb-dark-blue: #3F4C5E;
  --bb-salmon: #FF8A8A;
  --bb-purple: #8955FC;
  --bb-purple-light: #8850FE;
  --bb-grey: #202225;
  --bb-red: #ED2D45;
  --bb-white: #FFFFFF;
  --bb-black: #000000;
}

@font-face {
  font-family: "Dogica";
  src: url(../../fonts/OTF/dogica.otf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "VHSGothic";
  src: url(../../fonts/VHSGhotic/vhs-gothic.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Visitor";
  src: url(../../fonts/Visitor/visitor1.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Retro-Computer";
  src: url(../../fonts/TTF/retro_computer.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.share-tech-mono-regular {
  font-family: "Share Tech Mono", monospace;
  font-weight: 400;
  font-style: normal;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Retro-Computer", sans-serif;
}

body {
  position: relative;
  background: var(--bb-nav);
}

nav {
  position: fixed;
  width: 100%;
  height: 100px;
  z-index: 10;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  height: 100%;
}

.nav-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.nav-menu a,
.nav-menu button,
.nav-menu li {
  cursor: pointer;
}
.nav-menu__mobile {
  display: none;
}
.nav-menu__logo, .nav-menu__others {
  width: 20%;
}
.nav-menu__links {
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 60%;
  height: 100%;
  text-transform: uppercase;
  font-size: 1.4rem;
  color: var(--bb-white);
}
.nav-menu__link {
  padding: 1rem 2rem;
  background: var(--bb-grey);
  border-radius: 0.5rem;
  transition: background 0.4s;
}
.nav-menu__link:hover {
  background: var(--bb-green);
}
.nav-menu__others {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav-menu__others-link {
  padding: 2rem 2.6rem;
  background: var(--bb-purple);
  color: var(--bb-white);
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 600;
  transition: background 0.4s;
}
.nav-menu__others-link:hover {
  background: var(--bb-white);
  color: var(--bb-nav);
}

header video {
  max-height: 100vh;
  object-fit: cover;
  overflow-clip-margin: content-box;
  overflow: clip;
}

.tickets {
  background: url(../img/ticketsBg.png);
  background-size: cover;
  background-position: top;
  height: 100vh;
  font-family: "Retro-Computer", sans-serif;
}
.tickets__container {
  padding: 3rem 0 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4rem;
  height: 100%;
}
.tickets__container h2 {
  margin: 8rem 0 0;
  font-size: 3.2rem;
  text-transform: uppercase;
  color: var(--bb-white);
}
.tickets__box {
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  max-width: 1020px;
  width: 100%;
  height: 70%;
}
.tickets__ticket {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tickets__ticket img {
  width: 100%;
  height: auto;
  max-width: 285px;
  border-radius: 1.3rem;
  box-shadow: 0px 0px 15px 2px #696969;
}
.tickets__ticket button {
  margin: 7rem auto 0;
  padding: 1.2rem 2.4rem;
  font-size: 1.5rem;
  font-family: "Retro-Computer", sans-serif;
  border-radius: 1.4rem;
  border: none;
  color: var(--bb-white);
  box-shadow: 0 0 10px 0px #696969;
  transition: background 0.4s, color 0.4s;
  cursor: pointer;
}
.tickets__ticket:nth-of-type(1) button {
  background: var(--bb-purple-light);
}
.tickets__ticket:nth-of-type(2) button {
  background: var(--bb-green-light);
}
.tickets__ticket:nth-of-type(3) button {
  background: var(--bb-pink-light);
}
.tickets__ticket button:hover {
  background: var(--bb-white);
  color: var(--bb-black);
}

.line-up {
  position: relative;
  height: 100vh;
  background: url(../img/lineUpBg.png);
  background-size: cover;
  background-position: center;
  z-index: 0;
}
.line-up .line-up-graphs {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.line-up .crown,
.line-up .smileFace {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.line-up .crown {
  background: url(../../dist/img/crown.svg);
  background-size: 13%;
  background-position: top right;
  background-repeat: no-repeat;
}
.line-up .smileFace {
  background: url(../../dist/img/smileFace.gif);
  background-size: calc(10% + 10vw);
  background-position: bottom -12% left -5%;
  background-repeat: no-repeat;
}
.line-up .line-up-graphs .tv {
  position: absolute;
  max-width: 500px;
  width: 100%;
  height: auto;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
.line-up__box {
  margin: 0 auto;
  padding: 10rem 5rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1350px;
  height: 100%;
  border-radius: 1.6rem;
  z-index: 10;
}
.line-up__localization-box {
  padding: 2.4rem 3.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20%;
}
.line-up__localization-box h2 {
  padding: 2.2rem 5rem;
  font-size: 1.8rem;
  text-transform: uppercase;
  color: var(--bb-white);
  border: 5px solid #cdacc1;
  border-radius: 1.4rem;
}
.line-up__header {
  display: flex;
}
.line-up__title {
  padding: 2rem 0;
  color: var(--bb-pink-light);
  font-size: 1.8rem;
  text-transform: uppercase;
  width: 100%;
}
.line-up__btns {
  display: flex;
  gap: 2rem;
}
.line-up__btns button {
  padding: 2rem 2.4rem;
  color: var(--bb-white);
  border: 1px solid var(--bb-white);
  border-radius: 1rem;
  font-size: 1.4rem;
  font-weight: 900;
  text-transform: uppercase;
  border: none;
  transition: background 0.4s, color 0.4s;
  cursor: pointer;
}
.line-up__btns button:nth-of-type(1) {
  background: var(--bb-purple);
}
.line-up__btns button:nth-of-type(1):hover {
  background: var(--bb-white);
  color: var(--bb-purple);
}
.line-up__btns button:nth-of-type(2) {
  background: var(--bb-red);
}
.line-up__btns button:nth-of-type(2):hover {
  background: var(--bb-white);
  color: var(--bb-red);
}
.line-up__artist-box {
  padding: 2rem 0;
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  gap: 3rem;
  height: 50%;
  width: 100%;
}
.line-up__artist-box img {
  width: 100%;
  max-width: 207px;
  height: auto;
}
.line-up__footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 30%;
}
.line-up__bbtv, .line-up__yt {
  transform: scale(1);
  transition: transform 0.4s;
}
.line-up__bbtv:hover, .line-up__yt:hover {
  transform: scale(1.1);
}
.line-up__bbtv {
  max-width: 210px;
  font-size: 2.4rem;
  text-align: center;
  color: var(--bb-white);
}
.line-up__yt {
  max-width: 220px;
}
.line-up__yt img {
  width: 100%;
  height: auto;
}

.info {
  position: relative;
  background: url(../img/faqBg.png);
  background-size: cover;
  height: 100vh;
  z-index: 0;
}
.info .xSmall,
.info .xArrows {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.info .xSmall {
  background: url(../../dist/img/xSmall.svg);
  background-size: 3%;
  background-position: top 5% right 5%;
  background-repeat: no-repeat;
}
.info .xArrows {
  background: url(../../dist/img/xArrows.svg);
  background-size: 7%;
  background-position: bottom 4% left 2%;
  background-repeat: no-repeat;
}
.info__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.info__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  max-width: 992px;
  height: 100%;
}
.info__smile img, .info__kindness img {
  width: 100%;
  height: auto;
}
.info__kindness {
  max-width: 200px;
}
.info__title h2 {
  font-size: 3.4rem;
  color: var(--bb-white);
}
.info__faq {
  margin: 0 0 10rem;
  padding: 0 4rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  width: 100%;
}
.info__faq button {
  padding: 2rem 0;
  border-radius: 1.2rem;
  border: none;
  font-size: 1.8rem;
  font-family: "Retro-Computer", sans-serif;
  color: var(--bb-white);
  transition: background 0.4s, color 0.4s;
}
.info__faq button:nth-of-type(1) {
  background: var(--bb-pink-light);
}
.info__faq button:nth-of-type(1):hover {
  color: var(--bb-pink-light);
}
.info__faq button:nth-of-type(2) {
  background: var(--bb-green);
}
.info__faq button:nth-of-type(2):hover {
  color: var(--bb-green);
}
.info__faq button:nth-of-type(3) {
  background: var(--bb-blue);
}
.info__faq button:nth-of-type(3):hover {
  color: var(--bb-blue);
}
.info__faq button:nth-of-type(4) {
  background: var(--bb-purple);
}
.info__faq button:nth-of-type(4):hover {
  color: var(--bb-purple);
}
.info__faq button:hover {
  background: var(--bb-white);
}

.email {
  position: relative;
  height: 100vh;
  background: url(../img/emailBg.png);
  background-size: cover;
  background-position: center;
  z-index: 0;
}
.email .bar-code {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: url(../../dist/img/barCode.svg);
  background-size: 180px;
  background-position: center bottom 5px;
  background-repeat: no-repeat;
  opacity: 0.7;
}
.email::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../img/newsletterBg.png);
  background-size: cover;
  background-position: center;
  opacity: 0.6;
  z-index: -1;
  content: "";
}
.email__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.email__box {
  padding: 10rem 0 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  max-width: 640px;
  height: 100%;
}
.email__logo img {
  width: 100%;
  height: auto;
  max-width: 150px;
}
.email__title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15%;
  width: 100%;
}
.email__title h2 {
  display: flex;
  align-items: center;
  font-size: 4.2rem;
  color: var(--bb-white);
}
.email__title h2 img {
  padding: 0 2rem;
  width: 100%;
  height: auto;
  max-width: 180px;
}
.email__form {
  padding: 0 3rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
  height: 60%;
}
.email__form h3 {
  font-size: 2rem;
  color: var(--bb-white);
}
.email__form form {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
}
.email__form form input[type=text] {
  margin: 1rem 0;
  padding: 2rem;
  height: 60px;
  border-radius: 1.2rem;
  line-height: 0.4rem;
  border: none;
}
.email__form form input[type=text]::placeholder {
  font-family: "VHSGothic", sans-serif;
  font-size: 1.7rem;
  text-transform: uppercase;
  color: var(--bb-pink);
}
.email__form button {
  padding: 2rem 5rem;
  color: var(--bb-white);
  background: var(--bb-purple);
  border-radius: 1.2rem;
  border: none;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-family: "VHSGothic", sans-serif;
  transition: background 0.4s, color 0.4s;
}
.email__form button:hover {
  background: var(--bb-white);
  color: var(--bb-purple);
}
.email__agree-box {
  position: relative;
  padding: 2rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
}
.email__agree-box label {
  font-size: 0.9rem;
  color: var(--bb-white);
}
.email__agree-box input[type=checkbox] {
  position: absolute;
  opacity: 0;
  height: 25px;
  width: 22px;
}
.email__agree-box span {
  margin-right: 1rem;
  display: inline-block;
  background-color: white;
  box-sizing: border-box;
  width: 40px;
  height: 25px;
  border-radius: 0.2rem;
}
.email__agree-box span:has(> input:checked) {
  background-color: var(--bb-purple);
}

.socials {
  position: relative;
  height: 100vh;
  background: url(../img/socialsBg.png);
  background-size: cover;
  background-position: center;
  z-index: 0;
}
.socials .socials-graphs {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.socials .paper-sheet,
.socials .hand {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.socials .pattern {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
}
.socials .pattern--green {
  background: url(../../dist/img/patternGreen.svg);
  background-size: 5%;
  background-repeat: no-repeat;
  background-position: top 10% right 4%;
}
.socials .pattern--purple {
  background: url(../../dist/img/patternPurple.svg);
  background-size: 5%;
  background-repeat: no-repeat;
  background-position: top -2% left 0%;
}
.socials .pattern--pink {
  background: url(../../dist/img/patternPink.svg);
  background-size: 5%;
  background-repeat: no-repeat;
  background-position: bottom 0% left 5%;
  z-index: 0;
}
.socials .paper-sheet {
  background: url(../../dist/img/paperSheet.svg);
  background-size: 20%;
  background-position: top -30% right -10%;
  background-repeat: no-repeat;
}
.socials .hand {
  background: url(../../dist/img/hand.svg);
  background-size: 28%;
  background-position: bottom -20% left -7%;
  background-repeat: no-repeat;
}
.socials .socials-graphs .phone {
  position: absolute;
  max-width: 480px;
  width: 100%;
  height: auto;
  bottom: 0;
  left: 50%;
  transform: translateX(-40%);
}
.socials__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.socials__box {
  padding: 10rem 0 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1660px;
  height: 100%;
}
.socials__box h2 {
  margin: 8rem 0 0;
  font-size: 4.6rem;
  color: var(--bb-purple);
}
.socials__icons {
  padding: 0 2rem;
  align-self: flex-end;
  width: 20%;
}
.socials__icons h3 {
  margin: 0 0 3rem 0;
  font-size: 2.1rem;
  color: var(--bb-pink);
}
.socials__icons-list {
  display: flex;
  justify-content: space-between;
}
.socials__icons-list img {
  width: 100%;
  height: auto;
  max-width: 60px;
  transform: scale(1);
  transition: transform 0.4s;
}
.socials__icons-list img:hover {
  transform: scale(1.1);
}

.footer {
  background: var(--bb-white);
  width: 100%;
  height: 100%;
}
.footer__container {
  padding: 8rem 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.footer__container a {
  width: 15%;
  font-size: 1.2rem;
}
.footer__logos {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: 670px;
  width: 100%;
  background: var(--bb-black);
  border-radius: 1.6rem;
}
.footer__logos img {
  width: 100%;
  height: auto;
  max-width: 105px;
}

@media screen and (max-width: 992px) {
  .nav-menu {
    justify-content: flex-end;
  }
  .nav-menu__mobile {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav-menu__mobile button {
    color: var(--bb-white);
    background-color: transparent;
    border: none;
    width: 40px;
    cursor: pointer;
  }
  .nav-menu__logo, .nav-menu__mobile, .nav-menu__others {
    width: 30%;
  }
  .nav-menu__links {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100vw;
    height: 100vh;
    background: white;
  }
}
@media screen and (max-width: 769px) {
  .media__box-top, .media__box-bottom {
    flex-direction: column;
    aspect-ratio: 2/3;
    height: 100%;
  }
  .media__box-left, .media__box-right, .media__box-social-left {
    padding: 2rem;
    width: 100%;
    height: 100%;
  }
}
@media screen and (max-width: 600px) {
  .localization__box h2 {
    padding: 1rem 2.2rem;
    font-size: 1.2rem;
    text-align: center;
    line-height: 1.8;
  }
  .line-up__box {
    padding: 2rem;
  }
  .line-up__btns button {
    padding: 1.2rem 2rem;
  }
  .line-up__artist-box span {
    font-size: 1.2rem;
    text-align: center;
    line-height: 2;
  }
}
@media screen and (max-width: 565px) {
  nav {
    height: 60px;
  }
  .nav-menu__mobile button {
    width: 30px;
  }
  .nav-menu__others-link {
    padding: 1rem 1.2rem;
    font-size: 1.1rem;
  }
  .tickets {
    padding: 2rem 0 4rem;
    height: auto;
  }
  .tickets__container h2 {
    font-size: 2.3rem;
  }
  .tickets__box {
    flex-direction: column;
    gap: 4rem;
  }
  .tickets__ticket img {
    max-width: 280px;
  }
}